import qs from 'qs'
import axios from 'axios'
import url from './url'

export const UserActionTypes = {
  register: 'REGISTER',
  login: 'LOGIN',
  userQuery: 'USER_QUERY',
  userInfo: 'USER_INFO',
}

export default {
  namespace: false,

  state: {
    token: window.sessionStorage.getItem('token') || null
  },

  mutations: {
    setToken (state, data) {
      state.token = data
    },
    logout (state) {
      state.token = ''
      window.sessionStorage.removeItem('token')
    }
  },

  getters: {},

  actions: {
    // 
    [UserActionTypes.register]: (store, _payload) => {
      const { self, ...payload } = _payload
      axios.get(url.register, { params: payload })
        .then((data) => {
          if (data.data.err_code === 0) {
            //  
            self.$Message.success(``)
          } else {
            throw new Error(data.data.message)
          }
        })
        .catch(e => {
          e.message ? self.$Message.error(e.message) : self.$Message.error('')
        })
        .finally(() => {
          // TODO: handle finish
        })
    },

    // 
    [UserActionTypes.login]: (store, _payload) => {
      const { self, ...payload } = _payload
      axios.get(url.login, { params: payload })
        .then((data) => {
          if (data.data.err_code === 0) {
            self.$Message.success(``)
            store.commit('setToken', data.data.token)
            self.$router.replace(`/`)
          } else {
            throw new Error(data.data.message)
          }
        })
        .catch(e => {
          e.message ? self.$Message.error(e.message) : self.$Message.error('')
        })
        .finally(() => {
          // TODO: handle finish
        })
    },

    // 
    [UserActionTypes.userQuery]: (store, payload) => {
      axios.post(url.userQuery, qs.stringify(payload))
        .then((data) => {
          if (data.data.error_code === 0) {
            // TODO: 
          } else {
            // TODO: 
            throw new Error(``)
          }
        })
        .catch(e => {
          // TODO: handle error
        })
        .finally(() => {
          // TODO: handle finish
        })
    }
  }
}
