<template>
  <div id="login">
    <Card class="login-contain">
      <div class="left">
        <p class="header"></p>
        <p class="footer">Video conferencing</p>
      </div>
      <div class="right">
        <h3 class="right-title">{{info.title}}</h3>
        <Form ref="form" :model="form" :rules="formRules" class="form">
          <FormItem prop="user">
            <Input type="text" v-model="form.user" placeholder="" />
            <Icon type="md-person" class="icon"></Icon>
          </FormItem>
          <FormItem prop="password">
            <Input type="password" v-model="form.password" placeholder="" />
            <Icon type="md-lock" class="icon" />
          </FormItem>
          <FormItem>
            <Button type="primary" style="margin-top:10px;" @click="handleSubmit('form')" long shape="circle">{{info.button}}</Button>
          </FormItem>
        </Form>
        <router-link style="color:#635f5f; text-align:right; font-size:14px; text-decoration-line: underline;" :to="`/login/${type === '1' ? 2 : 1}`" :replace="true">{{info.footer}}</router-link>
      </div>
    </Card>
  </div>
</template>

<script>
import { ActionTypes } from '../store'

export default {
  name: 'Login',
  data () {
    const validateName = (rule, value, callback) => {
      if (this.type === '2') {
        // TODO:  
        callback()
        // 
        // this.$store.dispatch(ActionTypes.userQuery, { username: value })
      } else {
        callback()
      }
    }
    return {
      form: {
        user: '',
        password: ''
      },
      formRules: {
        user: [
          { required: true, message: '', trigger: 'change' },
          { validator: validateName, trigger: 'change' }
        ],
        password: [
          { required: true, message: '', trigger: 'change' },
        ]
      }
    }
  },
  computed: {
    type () {
      return this.$route.params.type
    },
    info () {
      return this.type === '1'
        ? {
          title: '/Login',
          footer: '',
          button: ''
        }
        : {
          title: '/Register',
          footer: '',
          button: ''
        }
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if (this.type === '2') {
            this.register()
          } else {
            this.login()
          }
        }
      })
    },
    login () {
      console.log(ActionTypes.login)
      this.$store.dispatch(ActionTypes.login, { username: this.form.user, password: this.form.password, self: this })
    },
    register () {
      this.$store.dispatch(ActionTypes.register, { username: this.form.user, password: this.form.password, self: this })
    }
  }
}
</script>

