<template>
  <div id="board">
    <div class="bar">
      <ButtonGroup>
        <Button :type="type === 0 ? `primary` : `default`" @click="type = 0"></Button>
        <Button :type="type === 1 ? `primary` : `default`" @click="type = 1"></Button>
        <Button :type="type === 2 ? `primary` : `default`" @click="type = 2"></Button>
        <Button :type="type === 3 ? `primary` : `default`" @click="type = 3"></Button>
        <Button :type="type === 4 ? `primary` : `default`" @click="type = 4"></Button>
        <Button :type="type === 5 ? `primary` : `default`" @click="type = 5"></Button>
      </ButtonGroup>
      <Button style="margin-left: 16px;" @click="resetCanvas(true)"></Button>
      <ColorPicker style="margin-left: 16px;" v-model="color" recommend/>
      <Input style="margin-left: 16px;width: 300px; display: inline-block;" v-model="text" v-if="type === 4" clearable placeholder="" />
      <span v-if="[2, 3].indexOf(this.type) > -1" style="margin-left: 16px;"><v-switch v-model="fill" /></span>
    </div>
      <div class="canvas-contain">
        <Canvas ref="canvas"
              class="canvas"
              @mousedown="e => onMouseDown(e)"
              @mousemove="e => onMouseMove(e)"
              @mouseup="e => onMouseUp(e)"
              @mouseout="resetFilter"
        />
        <Canvas ref="canvas-filter"
              class="canvas-filter"
        />
    </div>
  </div>
</template>

<script>
import io from 'socket.io-client'
import { mapState } from "vuex";

export default {
  name: 'board',
  data () {
    return {
      current: {
        x: 0,
        y: 0
      },
      cW: 1000,
      cH: 600,
      color: '',
      z: 1, // 
      text: '',
      drawing: false,
      canvas: null,
      canvasFilter: null,
      context: null,
      contextFilter: null,
      fill: false,
      socket: null,
      type: 0, // 0:  1:  2: , 3: , 4:; 5
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    onResize (e) {
      const width = window.document.body.clientWidth
      if (width < 1400) {
        this.z = 0.8
      } else if (width < 2000) {
        this.z = 1
      } else {
        this.z = 1.2
      }

      this.canvas.height = this.cH * this.z
      this.canvas.width = this.cW * this.z
      this.canvasFilter.width = this.cW * this.z
      this.canvasFilter.height = this.cH * this.z
    },

    onMouseDown (e) {
      this.drawing = true
      this.draw(e)
    },
    onMouseMove (e) {
      if (this.type === 5) {
        // 
        this.resetFilter()
        this.contextFilter.beginPath()
        this.contextFilter.strokeStyle = 'black'
        this.contextFilter.lineWidth = 1
        this.contextFilter.arc(e.offsetX, e.offsetY, 20, 0, 2 * Math.PI)
        this.contextFilter.fillStyle = '#ffffff'
        this.contextFilter.fill()
        this.contextFilter.stroke()
        this.contextFilter.closePath()
      }
      if (!this.drawing && this.type !== 4) { return }
      this.draw(e)
    },
    onMouseUp (e) {
      if (!this.drawing) { return }
      this.draw(e)
      this.drawing = false
    },

    getColor () {
      return this.color === '' ? `#000` : this.color
    },

    draw (e) {
      switch (this.type) {
        // 
        case 0:
          if (e.type === 'mousedown') {
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          } else {
            this.drawLine(this.current.x, this.current.y, e.offsetX || e.touches[0].offsetX, e.offsetY || e.touches[0].offsetY, this.getColor(), true)
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          }
          break

        // 
        case 1:
          if (e.type === 'mousedown') {
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          } else {
            this.resetFilter()
            this.contextFilter.beginPath()
            this.contextFilter.moveTo(this.current.x, this.current.y)
            this.contextFilter.strokeStyle = this.getColor()
            this.contextFilter.lineTo(e.offsetX || e.touches[0].offsetX, e.offsetY || e.touches[0].offsetY)
            this.contextFilter.lineWidth = 2
            this.contextFilter.stroke()
            this.contextFilter.closePath()
          }

          if (e.type === 'mouseup') {
            this.resetFilter()
            this.drawLine(this.current.x, this.current.y, e.offsetX || e.touches[0].offsetX, e.offsetY || e.touches[0].offsetY, this.getColor(), true)
          }

          break

        // 
        case 2:
          if (e.type === 'mousedown') {
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          } else {
            this.resetFilter()
            const d = Math.sqrt((this.current.x - e.offsetX) ** 2 + (this.current.y - e.offsetY) ** 2)
            this.contextFilter.beginPath()
            this.contextFilter.strokeStyle = this.getColor()
            this.contextFilter.lineWidth = 2
            this.contextFilter.arc(this.current.x, this.current.y, d, 0, 2 * Math.PI)
            this.contextFilter.fillStyle = this.getColor()
            this.fill && this.contextFilter.fill()
            this.contextFilter.stroke()
            this.contextFilter.closePath()
          }

          if (e.type === 'mouseup') {
            const d = Math.sqrt((this.current.x - e.offsetX) ** 2 + (this.current.y - e.offsetY) ** 2)
            this.resetFilter()
            this.drawArc(this.current.x, this.current.y, d, this.getColor(), this.fill, true)
          }

          break

        // 
        case 3:
          if (e.type === 'mousedown') {
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          } else {
            this.resetFilter()
            this.contextFilter.beginPath()
            this.contextFilter.strokeStyle = this.getColor()
            this.contextFilter.fillStyle = this.getColor()
            this.contextFilter.lineWidth = 2
            this.contextFilter.rect(this.current.x, this.current.y, e.offsetX - this.current.x, e.offsetY - this.current.y)
            // this.contextFilter.fillStyle="red";
            // this.contextFilter.fill();
            this.fill && this.contextFilter.fill()
            this.contextFilter.stroke()
            this.contextFilter.closePath()
          }

          if (e.type === 'mouseup') {
            this.resetFilter()
            this.drawRect(this.current.x, this.current.y, e.offsetX - this.current.x, e.offsetY - this.current.y, this.getColor(), this.fill, true)
          }

          break

        // 
        case 4:
          if (e.type === 'mousemove') {
            this.resetFilter()
            this.contextFilter.font = '20px ""'
            this.contextFilter.fillStyle = this.getColor()
            this.contextFilter.textAlign = 'center'
            this.contextFilter.textBaseline = 'top'
            this.contextFilter.fillText(this.text, e.offsetX, e.offsetY)
          } else if (e.type === 'mousedown') {
            this.resetFilter()
            this.drawText(this.text, e.offsetX, e.offsetY, this.getColor(), true)
          }
          break

        // 
        case 5:
          if (e.type === 'mousedown') {
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          } else {
            this.drawEarse(this.current.x, this.current.y, 20, true)
            this.current.x = e.offsetX || e.touches[0].offsetX
            this.current.y = e.offsetY || e.touches[0].offsetY
          }
          break
        default: break
      }
    },

    drawLine (x0, y0, x1, y1, color, emit) {
      this.context.beginPath()
      this.context.moveTo(x0, y0)
      this.context.lineTo(x1, y1)
      this.context.strokeStyle = color
      this.context.lineWidth = 2
      this.context.stroke()
      this.context.closePath()

      if (!emit) { return }
      let w = this.canvas.width
      let h = this.canvas.height

      this.emit({
        x0: x0 / w,
        y0: y0 / h,
        x1: x1 / w,
        y1: y1 / h,
        c: color,
        type: 0,
      })
    },

    drawArc (x, y, r, color, fill, emit) {
      this.context.beginPath()
      this.context.strokeStyle = color
      this.context.fillStyle = color
      this.context.lineWidth = 2
      this.context.arc(x, y, r, 0, 2 * Math.PI)
      // fill && this.context.fill()
      this.context.stroke()
      fill && this.context.fill()
      this.context.closePath()

      if (!emit) { return }

      let w = this.canvas.width
      let h = this.canvas.height

      this.emit({
        x: x / w,
        y: y / h,
        r: r / Math.sqrt(w ** 2 + h ** 2),
        f: fill,
        c: color,
        type: 2
      })
    },

    drawRect (x, y, _w, _h, color, fill, emit) {
      this.context.beginPath()
      this.context.strokeStyle = color
      this.context.fillStyle = color
      this.context.lineWidth = 2
      this.context.rect(x, y, _w, _h)
      this.context.stroke()
      fill && this.context.fill()
      this.context.closePath()

      if (!emit) { return }

      let w = this.canvas.width
      let h = this.canvas.height
      this.emit({
        x: x / w,
        y: y / h,
        w: _w / w,
        h: _h / h,
        c: color,
        f: fill,
        type: 3
      })
    },

    drawText (text, x, y, color, emit) {
      this.context.font = '20px ""'
      this.context.fillStyle = color
      this.context.textAlign = 'center'
      this.context.textBaseline = 'top'
      this.context.fillText(text, x, y)

      if (!emit) { return }

      let w = this.canvas.width
      let h = this.canvas.height


      this.emit({
        t: text,
        y: y / h,
        x: x / w,
        c: color,
        type: 4
      })
    },

    drawEarse (x, y, d, emit) {
      let w = this.canvas.width
      let h = this.canvas.height

      this.context.beginPath()
      this.context.strokeStyle = 'rgb(250,250,250)'
      this.context.lineWidth = 1
      this.context.arc(x, y, 0.018 * Math.sqrt(w ** 2 + h ** 2), 0, 2 * Math.PI)
      this.context.fillStyle = 'rgb(250,250,250)'
      this.context.fill()
      this.context.stroke()
      this.context.closePath()

      if (!emit) { return }

      this.emit({
        y: y / h,
        x: x / w,
        d: d / Math.sqrt(h ** 2 + y ** 2),
        type: 5
      })
    },

    emit (data) {
      console.log(`emit`)
      this.socket.emit('drawing', {
          mid: 123,
          token: this.user.token,
          data,
        })
    },

    onDrawingEvent (data) {
      console.log(`hearing`)
      const w = this.canvas.width
      const h = this.canvas.height
      switch (data.type) {
        case 0:
          this.drawLine(data.x0 * w, data.y0 * h, data.x1 * w, data.y1 * h, data.c)
          break
        case 2:
          this.drawArc(data.x * w, data.y * h, data.r * Math.sqrt(w ** 2 + h ** 2), data.c, data.f)
          break
        case 3:
          this.drawRect(data.x * w, data.y * h, data.w * w, data.h * h, data.c, data.f)
          break
        case 4:
          this.drawText(data.t, data.x * w, data.y * h, data.c)
          break
        case 5:
          this.drawEarse(data.x * w, data.y * h, 0.018 * Math.sqrt(w ** 2 + h ** 2))
          break
        case 6:
          this.resetCanvas()
          break
        default:
          break
      }
    },

    resetFilter () {
      console.log('react')
      this.contextFilter.clearRect(0, 0, this.canvasFilter.width, this.canvasFilter.height)
    },

    resetCanvas (emit) {
      this.context.rect(0, 0, this.canvas.width, this.canvas.height)
      this.context.fillStyle = 'rgb(250,250,250)'
      this.context.fill()
      if (emit) {
        this.emit('drawing', {
          type: 6
        })
      }
    }
  },
  mounted () {
    this.canvas = this.$refs.canvas
    this.canvasFilter = this.$refs['canvas-filter']

    // window.addEventListener('resize', this.onResize)
    this.onResize()

    this.context = this.$refs.canvas.getContext('2d')
    this.resetCanvas()

    this.contextFilter = this.canvasFilter.getContext('2d')
    this.resetFilter()
    this.socket = io.connect('http://3000.grcb2295.l3xy9scq.e2ea52.grapps.cn/')
    this.socket.on('drawing', this.onDrawingEvent)
  }
}
</script>

<style lang="scss">
#board {
  .canvas-contain{
    position: relative;
  }
  .bar {
    padding: 0 0 16px 0;
  }
  .canvas, .canvas-filter {
    margin: 0 auto;
    display: block;
  }
}

.canvas-filter{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  cursor: pointer;
  pointer-events: none;
}

</style>
